@import url('https://rsms.me/inter/inter.css');

* {
  box-sizing: border-box;
}

html,
body,
main,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  font-family: inter;
  color: black;
  background: #202025;
}

.content {
  width: 334px;
  height: 216px;
  background: #f0f0f0;
  border-radius: 3px;
  overflow: hidden;
  padding: 0;
}

.wrapper {
  padding: 1px;
  width: 668px;
  height: 432px;
  transform: scale(0.5);
  transform-origin: top left;
}


.boxStyle{
  position: absolute;
  top:50%;
  left:50%;
  transform: 'translate(-50%, -50%)';
  border: '2px solid #000';
  box-shadow: 24;
  padding: 4;
}
.buttonStart{
  background:#413F42;
  padding:20px 25px;
  border:none;
  font-size:22px;
  border-radius:6px;
  cursor:pointer;
  color:white;
  font-weight:bold;
  transition:0.3s ease all;
  box-shadow:1px 2px 3px rgba(0,0,0,0.3);
  &:hover{
  background:#6c6a6d;
  }
}

.boxHelp{
  position: absolute;
  bottom: 0;
  right: 0;
  padding: 14px;
  border-radius: 25px;
}

